import React,{useState,useEffect} from 'react'
import { Card, Button, Row, Col } from 'react-bootstrap';
import './JobPortal.css'
import axios from 'axios';
function JobPortal() {
    const cardStyles = {
      
        // width:'15em',
        // backgroundIimage: 'linear-gradient(to bottom right, yellow, orange)',
        // color: 'black', // Set text color to white for better visibility on the gradient background
        // boxShadow: '0 4px 8px rgba(0, 0, 0, 0.2)', // Add a subtle box shadow
        width:'15em',
       
  border: '10px solid rgba(0, 0, 0)',
  background: 'rgba(255, 255, 255)',
  color:'black',
  padding:'5px'
      };
    useEffect(async ()=>{
        const apiUrl = 'https://api.social3.club/job/get?page=1&size=2000';

        // Make a GET request to the API
        axios.get(apiUrl).then((res)=>{
            const jobsData=res.data.data.jobs
           console.log(res.data.data.jobs)
           const uniqueCrawlerCompanyNames = new Set();

// Filter the jobs to remove duplicates based on crawler company names
const filteredJobs = jobsData.filter((job) => {
  // Check if the crawler company name is not in the Set
  if (job.crawler_company && !uniqueCrawlerCompanyNames.has(job.crawler_company.name)) {
    // If not in the Set, add it and return true (keep this job)
    uniqueCrawlerCompanyNames.add(job.crawler_company.name);
    return true;
  }
  // If it's in the Set, return false (remove this job)
  return false;
});

// filteredJobs now contains the unique jobs based on crawler company names
console.log();
           setJobs(filteredJobs)
        }).then((err)=>{
           console.log(err)
        })

    },[])
    const [jobs,setJobs]=useState([])
    const [title,setTitle]=useState([])
  return (
    <div style={{color:'black'}}> 
    <div class="jobportal">
    {jobs.map((i)=>{ if(i.crawler_company && i.crawler_company.avatar_url)
        return(
            
                <Card style={cardStyles}>
        <Card.Img variant="top" style={{width:'10em',height:'7em'}} src={i.crawler_company.avatar_url} />
        <Card.Body>
          <br></br>
          <Card.Title>{i.title}</Card.Title>
         
          Location: 
          {i.location}
                <br></br>
              Company Name: {i.crawler_company.name}
              <br></br> 
              <button class="cbutton" onClick={()=>{window.open(`${i.apply_link}` , "_blank")}}>Apply</button>
               
               
        </Card.Body>
       &nbsp;&nbsp;&nbsp;&nbsp;
      </Card>
                
               
          
        )
    })}
    </div>
    </div>
  )
}

export default JobPortal